import utils from '../utils/Utils.js';
import { Page, Text, View, Document, StyleSheet} from '@react-pdf/renderer';
import React from 'react';


export default class Mail {
    constructor(props) {
        this.companyName = props.companyName;
        this.customerName = props.customerName; 
        this.postalCode = props.postalCode;
        this.finanzamt = props.finanzamt; 
        this.steuerNummer = props.steuerNummer; 
        this.kassenDienstleister = props.kassenDienstleister; 
        this.fristAblauf = props.fristAblauf; 
        this.availabilityDate = props.availabilityDate; 
        this.integrationsDatum = props.integrationsDatum;
    }

    downloadAsPDF() {
        const styles = StyleSheet.create({
            page: {
              flexDirection: 'row',
              backgroundColor: '#E4E4E4'
            },
            section: {
              margin: 30,
              padding: 20,
              flexGrow: 1, 
              fontSize: 12, 
              textAlign: 'justify',
            }
        });

        const mailText = this.getText();

        return <Document>
                <Page size="A4" style={styles.page}>
                    <View style={styles.section}>
                        <Text>{mailText}</Text>
                    </View>
                </Page>
            </Document>;
    }

    getMailBody() {
        const mailText = this.getText();
        return encodeURI(mailText);
    }

    getReceiverBlock() {
        return `An: ${this.finanzamt}\n`;
    }

    getDateBlock() {
        return `Datum: ${utils.getCurrentDate()}\n`;
    }

    getSenderBlock(){
        return `Abs.: ${this.companyName}\n`;
    }

    getSteuernummerBlock() {
        return `Steuernummer: ${this.steuerNummer}\n`;
    }

    getBetreffBlock() {
        return `Betreff: Antrag nach  148 AO  Verlngerung der Frist zur vollstndigen Implementierung \teiner Cloud-TSE\n`;
    }

    getAnredeBlock() {
        return `Sehr geehrte Damen und Herren,\n`;
    }

    getFirstParagraph() {  
        return `hiermit beantragen wir die Verlngerung der durch die Nichtbeanstandungsregelung eingerumten Frist ber den 31. Mrz 2021 hinaus gem  148 AO wegen Vorliegens unbilliger sachlichen Hrte bis zum ${this.fristAblauf} . Jedoch wollen wir klarstellen, dass es sich im folgenden Antrag um keine allgemeine Fristverlngerung handelt, sondern um einen bergangszeitraum in welchem eine TSE in Evaluierung beim Bundesamt fr Sicherheit in der Informationstechnik (BSI), zum Einsatz kommt. \n`;
    }

    getSecondParagraph() { 
        return `Wir beabsichtigen zur Einhaltung der Anforderungen des  146a AO die Nutzung der cloudbasierten TSE der fiskaly Germany GmbH welche durch unseren Kassen-Dienstleister ${this.kassenDienstleister} integriert wurde. ${this.kassenDienstleister} bietet seit dem ${this.availabilityDate} die fiskaly Cloud-TSE an und wir haben diese Vorabversion einer voll zertifizierten TSE bereits seit ${this.integrationsDatum ? this.integrationsDatum: this.availabilityDate} im Einsatz.  Aufgrund verschiedener von uns nicht zu verantwortender Umstnde hat sich die finale Zertifizierung der fiskaly Cloud-TSE verzgert. Eine detaillierte Stellungnahme dazu finden Sie im Anhang. Dementsprechend ist bereits jetzt erkennbar, dass die Inbetriebnahme der final zertifizierten Cloud-TSE nicht innerhalb der Frist bis zum 31. Mrz 2021 abgeschlossen werden kann. Wir planen mit der aktuellen zur Zertifizierung beim BSI eingereichten Version der fiskaly Cloud-TSE zu starten. Dieses Vorgehen ist auch mit der Fachabteilung des Bundesministerium fr Finanzen, Referat IVA 4 Steuern, abgestimmt. Konkret heit das, dass wir bereits die TSE im Einsatz haben und damit Geschftsflle manipulationssicher dokumentieren und Signaturen auf Belege drucken. \n`;
    }

    getThirdParagraph() { 
        return `Fr das oben beschriebene Vorgehen ersuchen wir um eine Erleichterung nach  148 AO um auf den voll zertifizierten Betrieb bis zum oben genannten Datum aufzursten. In diesem bergangszeitraum wird die aktuelle Version 1 der TSE von fiskaly zum Einsatz kommen. Dementsprechend wollen wir nochmal betonen, dass wir um keine allgemeine Fristverlngerung ansuchen. Fr das Upgrade auf den voll zertifizierten Betrieb (fiskaly Cloud TSE Version 2) sehen wir den Zeithorizont bis zum ${this.fristAblauf} als machbar. \n`;
    }

    getFourthParagraph(){ 
        return `Magebliche Ursache fr den bergangszeitraum sind - neben der noch ausstehenden Zertifizierungen - insbesondere die erhhten Anforderungen an die Umsetzung im Bereich der zertifizierten Anwenderumgebung beim Steuerpflichtigen. Darber hinaus sind die spten nderungen der technischen Spezifikationen welche durch das BSI verfasst und danach erst mit Ende November letzten Jahres an die Cloud-TSE Hersteller kommuniziert wurden. Diese spten nderungen hatten gravierenden Einfluss auf die Zertifizierungsverfahren aller Cloud-TSE Anbieter. Dementsprechend ist es in unserer Situation auch nicht mehr mglich die vollstndig zertifizierte fiskaly Cloud-TSE bis 31.3.2021 in Betrieb zu nehmen. \n`;
    }

    getFifthParagraph() { 
        return `Zum aktuellen Zeitpunkt erlaubt uns die Nutzung von fiskaly SIGN v1 die Signatur von Kassenbelegen und dadurch bereits eine erste Umsetzung von  146a AO. Dadurch weisen wir auch gerne die Prioritt der KassenSichV in unserem Unternehmen nach, wir haben uns bereits sehr frh und ausfhrlich mit dem Thema KassenSichV beschftigt und notwendige Anpassungen und Investitionen gettigt. Wir, als auch unser Hersteller, verweisen auf die unblich spt eingebrachten Anforderungen im laufenden Zertifizierungsverfahren, welche uns zu diesem Antrag zwingen.\n`;
    }

    getAnlageBlock() {
        return `Anlagen:  Allgemeine Stellungnahme Verzgerung Zertifizierung \rStatement Kassenhersteller und Integrator \r6er Schreiben`;
    }

    getText() { 
        let text; 

        text = `${text}${this.getReceiverBlock()} \n${this.getDateBlock()}\r`
        text = `${text}${this.getSenderBlock()} \n${this.getBetreffBlock()} \n${this.getAnredeBlock()} \n`
        text = `${text}${this.getFirstParagraph()} \n${this.getSecondParagraph()} \n`;
        text = `${text}${this.getThirdParagraph()} \n${this.getFourthParagraph()} \n`; 
        text = `${text}${this.getFifthParagraph()} \n${this.getAnlageBlock()}`;   

        return text;
    }

}; 